@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    font-family: 'Inter', system-ui, sans-serif;
  }
  
  body {
    @apply text-gray-900;
    background-color: #FAF9F6;
  }
}

@layer components {
  .btn {
    @apply inline-flex items-center justify-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-offset-2 transition-colors duration-200;
  }
  
  .btn-primary {
    @apply btn bg-primary-600 text-white hover:bg-primary-700 focus:ring-primary-500;
  }
  
  .btn-secondary {
    @apply btn bg-white text-gray-700 border-gray-300 hover:bg-gray-50 focus:ring-primary-500;
  }
  
  .btn-danger {
    @apply btn bg-red-600 text-white hover:bg-red-700 focus:ring-red-500;
  }
  
  .btn-success {
    @apply btn bg-green-600 text-white hover:bg-green-700 focus:ring-green-500;
  }
  
  .input {
    @apply block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-primary-500 focus:border-primary-500 sm:text-sm;
  }
  
  .textarea {
    @apply input resize-none;
  }
  
  .card {
    @apply shadow-sm border border-gray-200 rounded-lg;
    background-color: #FAF9F6;
  }
  
  .card-header {
    @apply px-6 py-4 border-b border-gray-200;
  }
  
  .card-body {
    @apply px-6 py-4;
  }
  
  .card-footer {
    @apply px-6 py-4 border-t border-gray-200;
    background-color: #FAF9F6;
  }
  
  .badge {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
  }
  
  .badge-primary {
    @apply badge bg-primary-100 text-primary-800;
  }
  
  .badge-secondary {
    @apply badge bg-gray-100 text-gray-800;
  }
  
  .badge-success {
    @apply badge bg-green-100 text-green-800;
  }
  
  .badge-warning {
    @apply badge bg-yellow-100 text-yellow-800;
  }
  
  .badge-danger {
    @apply badge bg-red-100 text-red-800;
  }
  
  .loading {
    @apply animate-spin rounded-full h-4 w-4 border-2 border-gray-300 border-t-primary-600;
  }
  
  .fade-in {
    @apply animate-fade-in;
  }
  
  .slide-up {
    @apply animate-slide-up;
  }
  
  .slide-down {
    @apply animate-slide-down;
  }
}

@layer utilities {
  .text-balance {
    text-wrap: balance;
  }
  
  .scrollbar-hide {
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  
  .scrollbar-hide::-webkit-scrollbar {
    display: none;
  }
  
  .prose-custom {
    @apply prose prose-gray max-w-none;
  }
  
  .prose-custom h1 {
    @apply text-2xl font-bold text-gray-900 mb-4;
  }
  
  .prose-custom h2 {
    @apply text-xl font-semibold text-gray-900 mb-3;
  }
  
  .prose-custom h3 {
    @apply text-lg font-medium text-gray-900 mb-2;
  }
  
  .prose-custom p {
    @apply text-gray-700 mb-4 leading-relaxed;
  }
  
  .prose-custom ul {
    @apply list-disc list-inside mb-4 space-y-1;
  }
  
  .prose-custom ol {
    @apply list-decimal list-inside mb-4 space-y-1;
  }
  
  .prose-custom li {
    @apply text-gray-700;
  }
  
  .prose-custom blockquote {
    @apply border-l-4 border-primary-500 pl-4 italic text-gray-600 mb-4;
  }
  
  .prose-custom code {
    @apply bg-gray-100 px-1 py-0.5 rounded text-sm font-mono;
  }
  
  .prose-custom pre {
    @apply bg-gray-900 text-gray-100 p-4 rounded-lg overflow-x-auto mb-4;
  }
  
  .prose-custom pre code {
    @apply bg-transparent p-0 text-gray-100;
  }
  
  .prose-custom a {
    @apply text-primary-600 hover:text-primary-700 underline;
  }
  
  .prose-custom img {
    @apply max-w-full h-auto rounded-lg;
  }
  
  .prose-custom table {
    @apply w-full border-collapse border border-gray-300 mb-4;
  }
  
  .prose-custom th {
    @apply border border-gray-300 px-4 py-2 bg-gray-50 font-medium text-left;
  }
  
  .prose-custom td {
    @apply border border-gray-300 px-4 py-2;
  }
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f5f9;
}

::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}

/* Focus styles */
.focus-visible:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

/* Selection styles */
::selection {
  background-color: #3b82f6;
  color: white;
}

/* Print styles */
@media print {
  .no-print {
    display: none !important;
  }
} 